import React from "react";
import './Persons.css';
import {Link} from "react-router-dom";
import tg from "./WebApp";

interface Props {
    titleText: string,
    svgSource: string,

    mainText: string;
    // arrayData: Array<Map<any, any>>
    arrayData: Array<Map<any, any>>
}

const Tiles: React.FC<Props> = ({ arrayData, svgSource, titleText, mainText }) => {
    const squares = [];
    let temp = [];
    for (let i = 0; i < arrayData.length; i++) {
        let data = arrayData[i]
        if (temp.length === 3) {
            squares.push(
                <div style={{width: '100%'}}  className='tilerow'>{temp}</div>)
            temp = []
        }
        temp.push(
                <div onClick={() => {
                    const requestOptions = {
                        method: 'POST',
                        headers: { 'Content-Type': 'text/plain' },
                        body: JSON.stringify({ userId: 123, link: data.get('link')})
                    };
                    console.log(JSON.stringify({ data: tg }))
                    fetch('http://91.201.115.226:8765/change_mode/', requestOptions).then(r => console.log(r))
                }} className="item" data-s={data.get('link')}>
                    <img src={data.get('src')}/>
                    <h4 > {data.get('title')} </h4>
                </div>
        );
    }


    return (
        <div className='main'>
            <div style={{
                alignItems: "center"
            }} className="container">
                <p>{titleText}</p>
                <img src={svgSource} alt="Image" />
            </div>
            <p>{mainText}</p>

            <div className='parent'>
                {squares}
            </div>


        </div>
    );
};

export default Tiles;

